import React from "react";
import axios from "axios";
import { useState } from "react";

import { useEffect } from "react";
function DataCollection() {
  const [skills, setSkills] = useState([]);
  const [lessons, setLessons] = useState([]);
  const [topics, setTopics] = useState([]);
  const [topic, setTopic] = useState([]);

  const [skillsId, setSkillsId] = useState(
    "34167af1-49ec-4330-92e0-eceb0aa06cf8"
  );
  const [lessonsId, setLessonsId] = useState("");
  const [topicsId, setTopicsId] = useState("");
  const [topicId, setTopicId] = useState("");

  let searchSkills = (e) => {
    e.preventDefault();
    axios
      .get(`https://learn.steyp.com/api/v1/learn/skills/${skillsId}/`, {
        headers: {
          authorization: `Bearer CqWPevZvDUerCbvivjIlr1K1U2CuPr`,
        },
      })
      .then((response) => setSkills(response.data.data));
  };
  let searchLessons = (e) => {
    e.preventDefault();
    axios
      .get(`https://learn.steyp.com/api/v1/learn/lessons/${lessonsId}/`, {
        headers: {
          authorization: `Bearer CqWPevZvDUerCbvivjIlr1K1U2CuPr`,
        },
      })
      .then((response) => setLessons(response.data.data));
  };
  let searchTopics = (e) => {
    e.preventDefault();
    axios
      .get(`https://learn.steyp.com/api/v1/learn/topics/${topicsId}/`, {
        headers: {
          authorization: `Bearer CqWPevZvDUerCbvivjIlr1K1U2CuPr`,
        },
      })
      .then((response) => setTopics(response.data.data));
  };
  let searchTopic = (e) => {
    e.preventDefault();
    axios
      .get(`https://learn.steyp.com/api/v1/learn/topic/${topicId}/`, {
        headers: {
          authorization: `Bearer CqWPevZvDUerCbvivjIlr1K1U2CuPr`,
        },
      })
      .then((response) => setTopic(response.data.data));
  };
  console.log(topic);
  return (
    <div className="p-10 text-on-surface dark:text-on-surface-dark flex justify-between gap-2">
      <div className="w-1/4">
        <h2 className="mb-2 text-xl">Search Skills</h2>
        <div className="mb-2 flex flex-col w-full justify-end">
          <input
            className="px-4 py-2 text-on-surface rounded-3xl mr-2 w-full "
            value={skillsId}
            type="text"
            onChange={(e) => setSkillsId(e.target.value)}
            placeholder="Skill Id"
          />
          <button
            className=" cursor-pointer block mt-2 px-4 py-2 bg-primary-80 text-on-secondary-container rounded-3xl"
            onClick={searchSkills}
          >
            Search
          </button>
        </div>

        {skills.map((item) => (
          <div className="mb-2 p-4 bg-surface-3-dark rounded-3xl">
            <input
              className="text-on-surface rounded-3xl w-full cursor-pointer"
              type="text"
              onClick={() => {
                navigator.clipboard.writeText(item.name);
              }}
              value={item.name}
            />
            <input
              className="text-on-surface rounded-3xl w-full cursor-pointer"
              type="text"
              onClick={() => {
                navigator.clipboard.writeText(item.id);
              }}
              value={item.id}
            />
          </div>
        ))}
      </div>
      <div className="w-1/4">
        <h2 className="mb-2 text-xl">Search Lessons</h2>
        <div className="mb-2 flex flex-col w-full justify-end">
          <input
            className="px-4 py-2 text-on-surface rounded-3xl mr-2 w-full"
            value={lessonsId}
            onChange={(e) => setLessonsId(e.target.value)}
            placeholder="Lesson Id"
          ></input>
          <button
            className=" cursor-pointer block mt-2 px-4 py-2 bg-primary-80 text-on-secondary-container rounded-3xl"
            onClick={searchLessons}
          >
            Search{" "}
            <span className="text-white ml-2 bg-on-background rounded-full text-sm px-2 py-1">
              {lessons.length}
            </span>
          </button>
        </div>

        {lessons.map((item) => (
          <div className="mb-2 p-4 bg-surface-3-dark rounded-3xl">
            <p></p>
            <input
              className="text-on-surface rounded-3xl w-full cursor-pointer"
              type="text"
              onClick={() => {
                navigator.clipboard.writeText(item.name);
              }}
              value={item.name}
            />
            <input
              className="text-on-surface rounded-3xl w-full cursor-pointer"
              type="text"
              onClick={() => {
                navigator.clipboard.writeText(item.id);
              }}
              value={item.id}
            />
          </div>
        ))}
      </div>
      <div className="w-1/4">
        <h2 className="mb-2 text-xl">Search Topics</h2>
        <div className="mb-2 flex flex-col w-full justify-end">
          <input
            className="px-4 py-2 text-on-surface rounded-3xl mr-2 w-full"
            value={topicsId}
            onChange={(e) => setTopicsId(e.target.value)}
            placeholder="Topics Id"
          ></input>
          <button
            className=" cursor-pointer block mt-2 px-4 py-2 bg-primary-80 text-on-secondary-container rounded-3xl"
            onClick={searchTopics}
          >
            Search{" "}
            <span className="text-white ml-2 bg-on-background rounded-full text-sm px-2 py-1">
              {topics.length}
            </span>
          </button>
        </div>

        {topics.map((item) => (
          <div className="mb-2 p-4 bg-surface-3-dark rounded-3xl">
            <input
              className="text-on-surface rounded-3xl w-full cursor-pointer"
              type="text"
              onClick={() => {
                navigator.clipboard.writeText(item.name);
              }}
              value={item.name}
            />
            <input
              className="text-on-surface rounded-3xl w-full cursor-pointer"
              type="text"
              onClick={() => {
                navigator.clipboard.writeText(item.id);
              }}
              value={item.id}
            />
          </div>
        ))}
      </div>
      <div className="w-1/4">
        <h2 className="mb-2 text-xl">Search Topic</h2>
        <div className="mb-2 flex flex-col w-full justify-end">
          <input
            className="px-4 py-2 text-on-surface rounded-3xl mr-2 w-full"
            value={topicId}
            onChange={(e) => setTopicId(e.target.value)}
            placeholder="Topic Id"
          ></input>
          <button
            className=" cursor-pointer block mt-2 px-4 py-2 bg-primary-80 text-on-secondary-container rounded-3xl"
            onClick={searchTopic}
          >
            Search
          </button>
        </div>

        <div className="mb-2 p-4 bg-surface-3-dark rounded-3xl">
          <input
            className="text-on-surface rounded-3xl w-full cursor-pointer"
            type="text"
            onClick={() => {
              navigator.clipboard.writeText(topic.name);
            }}
            value={topic.name}
          />
          <input
            className="text-on-surface rounded-3xl w-full cursor-pointer"
            type="text"
            onClick={() => {
              navigator.clipboard.writeText(topic.id);
            }}
            value={topic.id}
          />
          <p>Image:</p>
          <input
            className="text-on-surface rounded-3xl w-full cursor-pointer"
            type="text"
            onClick={() => {
              navigator.clipboard.writeText(topic.image);
            }}
            value={topic.image}
          />
          <p>Video:</p>
          <textarea
            className="text-on-surface h-60 w-full rounded-3xl box-border"
            type="text"
            onClick={() => {
              navigator.clipboard.writeText(topic.playlists.auto.playlist);
            }}
            value={JSON.stringify(topic.playlists)}
          />
        </div>
      </div>
    </div>
  );
}

export default DataCollection;
